<template>
  <div @scroll.native="handlerScroll">
    <b-carousel
      controls
      indicators
      style="text-shadow: 1px 1px 2px #333;"
      :interval="6000"
      img-width="1024"
      img-height="480"
      background="#ababab"
      v-model="slide"
      v-touch:swipe.left="swipeLeft"
      v-touch:swipe.right="swipeRight"
    >
      <b-carousel-slide v-for="(item,index) of img" :key="index">
        <h1>{{captions[index]}}</h1>
        <p>{{texts[index]}}</p>
        <b-img slot="img" :src="item" fluid-grow alt="image" class="img"/>
      </b-carousel-slide>
    </b-carousel>
    <br>
    <b-container fluid id="scrollTop">
      <b-row>
        <b-col cols="8">
          <div id="listgroup-ex">
            <h1 id="list-item-1">{{slogan}}</h1>
            <p class="p" v-if="showB">{{text1}}</p>
            <h1 id="list-item-2">{{slogan}}</h1>
            <p class="p" v-if="showC">{{text1}}</p>
          </div>
        </b-col>
        <b-col cols="4">
          <b-list-group v-b-scrollspy:listgroup-ex>
            <b-list-group-item
              v-for="(title,index) of title"
              :key="index"
              :href="title"
            >{{label[index]}}</b-list-group-item>
          </b-list-group>
        </b-col>
      </b-row>
    </b-container>
    <div id="up" @click="backTop">
      <i class="fa fa-angle-up"></i>
    </div>
  </div>
</template>

<script>
import image from "../../assets/Banner/*.jpg";
import webp from "../../assets/Banner/*.webp";
export default {
  data() {
    return {
      slide: 0,
      sliding: null,
      texts: ["flower", "dark", "quiet", "light"],
      captions: ["Hello", "Admit defeat", "I love you", "promised you"],
      img:
        document
          .createElement("canvas")
          .toDataURL("image/webp")
          .indexOf("data:image/webp") == 0
          ? webp
          : image,
      slogan: "Welcome to my website",
      showB: false,
      showC: false,
      title: ["#list-item-1", "#list-item-2"],
      label: ["1", "2"],
      text1: `,,!,,,!,,,!,,,!,!,,!!,,!,,,!,!!,!,,!,!! !!`
    };
  },
  watch: {
    slide() {
      this.$store.state.count = this.slide;
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handlerScroll);
    (document.getElementById("up").style.display = "none"),
      setTimeout(() => {
        this.showB = true;
      }, 0);
    setTimeout(() => {
      this.showC = true;
    }, 0);
  },
  methods: {
    swipeLeft() {
      if (this.slide + 1 == 4) this.slide = 0;
      else this.slide++;
    },
    swipeRight() {
      if (this.slide - 1 == -1) this.slide = 3;
      else this.slide--;
    },
    handlerScroll() {
      if (
        document.getElementById("scrollTop").getBoundingClientRect().top < 0
      ) {
        document.getElementById("up").style.display = "block";
      } else {
        document.getElementById("up").style.display = "none";
      }
    },
    backTop() {
      event.preventDefault();
      var timer = setInterval(() => {
        /**/
        var scroll =
          document.documentElement.scrollTop |
          document.body.scrollTop; /**/
        window.scrollTo(
          0,
          scroll - Math.floor(scroll / 3)
        ); /**/
        if (scroll < 6) {
          window.scrollTo(0, 0);
          clearInterval(timer);
        } /**/
      }, 15);
    }
  },
  destroyed() {
    window.removeEventListener("scroll", this.handlerScroll);
  }
};
</script>
